@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding: 6px 20px;
    position:-webkit-sticky;
    position:sticky;
    top: 0;
    z-index: 100;
    background:white;
}

.header_left{
    display: flex;
    align-items:center;
}

.header_left > .header_left_menuIcon {
    color: #606060;
    cursor: pointer;
}

.header_left > img{
    height:50px;
    width: 80px;
    margin-left: 20px;
    cursor: pointer;
}

.header_center{
    display: flex;
    align-items:center;
    border: 1px solid lightgray;
    border-radius: 2px;
    width: 48%;
}

.header_center > input{
    flex:1 1;
    border: none;
    outline-color: blue;
    border-right: 1px solid lightgray;
    padding: 6px 10px;
    font-size: medium;
}

.header_center_Link{
    display: flex;
}

.inputBtn {
    border: none;
    padding: 4px 20px;
    background: #F0F0F0;
    color: grey;
    font-size:22px;
    text-decoration: none;
}


.header_right{
    display: flex;
    align-items:center;
}

.header_right > .header_right_icons{
    margin: 0 10px;
    color: #606060;
    cursor: pointer;
}

.userProfileLogo{
    height: 35px !important;
    width: 35px !important;
    cursor: pointer;
}

hr{
    height: 1px;
    border:0;
    background: lightgrey;
    margin-top: 10px;
    margin-bottom: 10px;
}
.sideBarRow_Link{
    text-decoration: none;
}

.sidebarRow{
    display: flex;
    padding: 10px 20px;
    align-items: center;
    cursor: pointer;
}

.sidebarRow_icon{
    font-size: 22px !important;
}

.sidebarRow > .sidebarRow_icon{
    color: #606060;
}

.sidebarRow > h4{
    flex: 1 1;
    font-size:13.6px;
    margin-left: 20px;
    font-weight: medium;
    color: black;
}

.sidebarRow:hover {
    transition: .1ms;
    background: rgb(237, 237, 237);
}

.sidebarRow:hover > .sidebarRow_icon {
    transition: .3ms;
    color: #FF0000;
}

.selected {
    background: lightgray;
}

.selected > .sidebarRow_icon {
    color: #FF0000;
}

.selected > h4 {
    font-size: 14.5px;
}
.sideBar {
    flex: 0.18 1;
    display: flex;
    flex-direction: column;
    padding-top: 5px;
}
.videoCard {
    width: 245px;
    margin: 10px 12px;
    cursor: pointer;
}

.videoCard > img {
    width: 255px;
}

.videoCard > .videoCard_detail {
    display: flex;
    margin-top: 5px;
}

.videoCard > .videoCard_detail > .channelLogo {
    height: 35px !important;
    width: 35px !important;
}

.videoCard > .videoCard_detail > .videoCard_detail_text {
    padding-left: 12px;
}

.videoCard > .videoCard_detail > .videoCard_detail_text > h4 {
    margin-bottom: 5px;
}

.videoCard > .videoCard_detail > .videoCard_detail_text > p{
    display:flex;
    align-items:center;
    color: grey;
    font-size: small !important;
}
.recommendedVideos{
    flex: 0.82 1;
    background: #f9f9f9;
    padding-top: 15px;
    padding-bottom: 0;
    padding-left: 15px;
}

.recommendedVideos > .recommendedVideos_videosSection{
    display: flex;
    flex-wrap: wrap;
    padding-top: 10px;
}
.sYoutubeChannelCard {
    display: flex;
    width: 80%;
    align-items:center;
}

.sYoutubeChannelCard > .channelLogo{
    height: 140px;
    width: 140px;
    margin: 5px 120px 5px 80px;
    cursor: pointer;
}

.sYoutubeChannelCard > .sYoutubeChannelCard_text {
    cursor: pointer;
}

.sYoutubeChannelCard > .sYoutubeChannelCard_text > h3 {
    display: flex;
    align-items:center;
    color: black !important;
    font-size:18px !important;
}

.sYoutubeChannelCard > .sYoutubeChannelCard_text > h3 > .verifiedIcon {
    margin-left: 5px;
    color: #606060;
    font-size: medium !important;
}

.sYoutubeChannelCard > .sYoutubeChannelCard_text > h3,p {
    color: #606060;
    font-size: small;
    margin-bottom: 10px;
}
.sYoutubeChannelVideoCard{
    display: flex;
    margin: 20px 5px 5px 0;
    cursor: pointer;
}

.sYoutubeChannelVideoCard > img {
    width: 340px;
    margin-right: 10px;
}

.sYoutubeChannelVideoCard > .sYoutubeChannelVideoCard_text > .channelLNV {
    display: flex;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

.sYoutubeChannelVideoCard > .sYoutubeChannelVideoCard_text > .channelLNV > .channelLogo {
    height: 26px;
    width: 26px;
    margin-right: 10px;
}

.sYoutubeChannelVideoCard > .sYoutubeChannelVideoCard_text > .channelLNV > .verifiedIcon {
    font-size: small;
    margin-left: 5px;
}
.searchPage{
    flex: 0.8 1;
    background: #f9f9f9;
    padding-top: 20px;
    padding-bottom: 0;
    padding-left: 20px;
}

.searchPage > .filter {
    display: flex;
    align-items: center;
    color: #606060;
    font-size: 14px;
    cursor: pointer;
}
         
.searchPage > .filter > .searchPageFilterIcon{
    margin-right:8px;
    font-size: 24px !important;
}
.app_content{
    display: flex;
}
